.box {
  position: absolute;
  display: none;
  opacity: 0;
  visibility: hidden;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.box__backdrop {
  height: 100vh;
  width: 100vw;
  background-color: var(--warna-hitam);
}
.box__container {
  opacity: 1;
  visibility: visible;
  border-radius: 1.25rem;
  width: 90%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--warna-putih);
}
@media screen and (min-width: 37.5rem) {
  .box__container {
    width: 34.375rem;
  }
}
.box__container h1 {
  text-align: center;
}
.box__container img {
  cursor: pointer;
  margin: 1.25rem;
  width: 1.875rem;
  transform: rotate(45deg);
  transition: transform 0.5s ease;
}
.box__container img:hover {
  transform: rotate(135deg);
}
.box__container p {
  margin: 0.75rem 0 0.75rem 0;
  text-align: center;
  font-size: 0.75rem;
}
.box__container p span {
  cursor: pointer;
  font-weight: 700;
  transition: all 0.5s ease;
}
.box__container p span:hover {
  border-radius: 3.125rem;
  padding: 0.1875rem 0.875rem 0.1875rem 0.875rem;
  color: var(--warna-putih);
  background-color: var(--warna-hitam);
}
.box__container form {
  width: 80vw;
  max-width: 25rem;
  display: flex;
  flex-direction: column;
  margin: 1rem auto 5rem auto;
}
@media screen and (min-width: 37.5rem) {
  .box__container form {
    width: 25rem;
  }
}
.box__container form label {
  margin-bottom: 0.375rem;
}
.box__container form input {
  width: 80vw;
  max-width: 25rem;
  padding: 0.375rem 0 0.375rem 1rem;
  border: solid var(--warna-hitam) 0.0625rem;
  border-radius: 3.125rem;
  margin-bottom: 1rem;
}
@media screen and (min-width: 37.5rem) {
  .box__container form input {
    width: 25rem;
  }
}
.box__container .button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.box__container .button__login button {
  max-width: 25rem;
  width: 80vw;
  cursor: pointer;
  border: solid 0.0625rem black;
  border-radius: 3.125rem;
  padding: 0.5rem 0;
  font-weight: 500;
  font-size: 1.125rem;
  color: var(--warna-putih);
  background-color: black;
  transition: all 0.5s ease;
}
.box__container .button__login button:hover {
  color: var(--warna-hitam);
  background-color: var(--warna-putih);
  border: solid 0.0625rem var(--warna-hitam);
}
.box__container .button__signup button {
  max-width: 25rem;
  width: 80vw;
  cursor: pointer;
  border: solid 0.0625rem black;
  border-radius: 3.125rem;
  padding: 0.5rem 0;
  font-weight: 500;
  font-size: 1.125rem;
  color: var(--warna-putih);
  background-color: black;
  transition: all 0.5s ease;
}
.box__container .button__signup button:hover {
  color: var(--warna-hitam);
  background-color: var(--warna-putih);
  border: solid 0.0625rem var(--warna-hitam);
}
.box__container .box__atas {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.box__container .box__atas form {
  margin: 0 0 0 1rem;
  width: 100%;
}
.box__container .box__atas form input {
  color: black;
  background-color: unset;
  border: none;
  width: 100%;
  font-size: 1.125rem;
  margin: 0;
  padding: 0;
}
.box__container .box__atas form input:focus {
  outline: none;
}
.box__container .box__bawah {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.box__container .box__bawah form {
  padding: 0 1rem 0.625rem 1rem;
  max-width: 100%;
  margin: 0;
  width: 100%;
}
.box__container .box__bawah form textarea {
  resize: none;
  border: none;
  width: 100%;
  height: 20rem;
  font-size: 14px;
}
.box__container .box__bawah form textarea:focus {
  outline: none;
}
.box__container .box__bawah .button {
  padding-bottom: 0.625rem;
}

.box.aktif {
  display: flex;
  opacity: 1;
  visibility: visible;
}